export class FileName implements IFileNameParam {

    constructor(private iFileNameParam: IFileNameParam) {
        this.fileRFPDbId = iFileNameParam.fileRFPDbId;
        this.Name = iFileNameParam.Name;
        this.Path = iFileNameParam.Path;
        this.HtmlFile = iFileNameParam.HtmlFile;
    }
    public fileRFPDbId?: number;
    public Name?: string;
    public Path?: string;
    HtmlFile?: string;
}


export interface IFileNameParam {
    fileRFPDbId?: number;
    Name?: string;
    Path?: string;
    HtmlFile?: string;

}